import AsyncLoad from './components/async_load'

export default [
  {
    name: '',
    icon: 'home',
    path: '/',
    component: AsyncLoad(() => import('./pages/home'))
  },
  {
    name: '',
    icon: 'home',
    path: '/detail/:id',
    component: AsyncLoad(() => import('./pages/movie/detail'))
  },
]


